body {
  font-size: 20px;
  line-height: 22px;
  color: #474747;
}
body .header {
  position: unset!important;
}
body .content {
  padding-top: 25px;
  width: 100%;
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 1fr 7%;
}
body .content .container {
  grid-area: container;
  display: grid;
  grid-template-areas: "aside breadcrumb breadcrumb" "aside catalog popProd" "order order order";
  grid-template-columns: 25% 1fr 15%;
}
body .content .container .content__aside {
  grid-area: aside;
  width: 90%;
}
body .content .container .content__aside div {
  background-color: #176975;
}
