@import "variables";

body{
  #delivery {
    width: 735px;
    min-height: 250px;
    padding: 25px 20px;
    border-radius: 5px;
    background: #fff;
    position: fixed;
    top: 45%;
    left: 50%;
    margin-top: -130px;
    margin-left: -367.5px;
    display: none;
    opacity: 0;
    z-index: 20;
    .cross {
      width: 100%;
      text-align: right;
      color: @mainColor;
    }
    #deliveryClose {
      width: 21px;
      height: 21px;
      position: absolute;
      top: 10px;
      right: 10px;
      cursor: pointer;
      display: block;
    }
    h5 {
      font: 1.125em/1.25em @sansbold;
      color: @mainColor;
    }
    p {
      font: 1em/1.125em @sansregular;
      color: #474747;
    }
  }

  #overlay {
    z-index: 15;
    position: fixed;
    background-color: #000;
    opacity: .8;
    filter: alpha(opacity=80);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    cursor: pointer;
    display: none;
  }
}